import React, {ChangeEvent, KeyboardEvent} from 'react'
import s from './Greeting.module.css'
import src from "./../../commonStyles/errror logo.png";

export type GreetingPropsType = {
    name: string // need to fix any
    setNameCallback: (x: any) => void // need to fix any
    addUser: (name:string) => void // need to fix any
    error: string // need to fix any
    totalUsers: number// need to fix any
    onKeyPressName: (e: KeyboardEvent<HTMLInputElement>) => void
    onBlur: (e: ChangeEvent<HTMLInputElement>) => void

}

// презентационная компонента (для верстальщика)
const Greeting: React.FC<GreetingPropsType> = (
    {name, setNameCallback, addUser, error, totalUsers, onKeyPressName} // деструктуризация пропсов
) => {
    const inputClass = error ? s.inputError : s.normal// need to fix with (?:)
    const disabled = !name ? s.disabled : s.button// need to fix with (?:)
    const logo = src

    return (
        <div className={s.someClass}>
            <input value={name}
                   onChange={setNameCallback}
                   className={`${s.normal} ${inputClass}`}
                   onKeyPress={onKeyPressName}
                   placeholder={'Введите имя'}/>
            {/*<span>{error}</span>*/}
            <span>
                <button className={disabled} onClick={()=>addUser(name)} onBlur={setNameCallback} disabled={!name}>add</button>

            </span>

            <div className={s.totalUsers}>{totalUsers}</div>
            <div className={s.error}>{error}</div>
        </div>
    )
}

export default Greeting
